var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('body',{attrs:{"scoped":""}},[_vm._ssrNode("<div data-v-68a92ee0><div class=\"project-desc col-12 offset-md-2 col-md-8 mt-5\" data-v-68a92ee0><img"+(_vm._ssrAttr("src",require("assets/img/BORE/bore_square_logo.PNG")))+" style=\"width:500px; item-align: center;\" data-v-68a92ee0> <h2 style=\"text-align:center;\" data-v-68a92ee0>SPECS</h2> <h4 style=\"list-style-type:none\" data-v-68a92ee0><li data-v-68a92ee0><strong data-v-68a92ee0>GENRE</strong>: A survival / strategy game featuring puzzles, in-depth lore, and adventure!</li> <li data-v-68a92ee0><strong data-v-68a92ee0>CREATED</strong>: September - Decemeber 2020</li> <li data-v-68a92ee0><strong data-v-68a92ee0>DEVS</strong>:</li><li style=\"text-align: center;\" data-v-68a92ee0><strong data-v-68a92ee0>CODE</strong><br data-v-68a92ee0>Robin Couwenberg, Heather Cochrane <br data-v-68a92ee0><strong data-v-68a92ee0>ART</strong><br data-v-68a92ee0> Matthew Jenkins, Courtney Spivey <br data-v-68a92ee0><strong data-v-68a92ee0>DESIGN</strong><br data-v-68a92ee0>Courtney Spivey, Matthew Jenkins, Wayne Yiqun Guo, Michelangelo Bedetti <br data-v-68a92ee0><strong data-v-68a92ee0>PRODUCTION</strong><br data-v-68a92ee0>Michelangelo Bedetti </li></h4></div> <div class=\"project-desc col-12 offset-md-2 col-md-8 mt-5\" data-v-68a92ee0><h2 style=\"text-align:center;\" data-v-68a92ee0>OVERVIEW</h2> <h4 data-v-68a92ee0>BORE was the result of a semester long project at the University of Abertay. We were told to create a god game and had full creative control of how it looked, worked and felt. After a few initial pitch ideas, we went with Matthew's ideas of a dystopian mining game, where the objective was to discover caves and dig to the bottom. The targeted audience was Everyone, although there were some darker themes to be explored in the game later on, most of it was not implemented so it was suitable for all ages &amp; demographics. We went with mobile development first, and have also created a PC version.The mobile version utilized more touch feedback and we wanted to cater specifically for phones first, so the experiences are slightly different.</h4> <h4 data-v-68a92ee0>BORE begins with the escape of our main characters from the toxic wasteland above ground to the unknown depths below. As Earth's atmosphere crumbled, so did its civilizations. Knowledge of technology dimmed, people began  to worship beings from ancient myths, and many credited useful tools to their mythical creations. Driven by a desire to survive and one day perhaps to return to the surface, the inhabitants of BORE burrow deeper and deeper beneath the earth's surface, discovering caves, mysteries, and the truth of their humanity.</h4> <img"+(_vm._ssrAttr("src",require("assets/img/BORE/work.PNG")))+" style=\"width:65vw;\" data-v-68a92ee0> <h2 data-v-68a92ee0> RESPONSIBILITIES </h2> <h4 data-v-68a92ee0> I worked on the UI of the game and created the artwork for it. I also worked on the character art. </h4> <h4 data-v-68a92ee0>Matthew and I had distinctly different styles and it wasn't until almost the end of the project where we understood that our differences did not create a cohesive game. We had the same ideas and concepts in mind, but our executions were always different. As he worked on the environmental art that would give a distinct feeling to our game, we decided to attempt to marry my style to his instead of the other way around. Discovering a difference in execution was eye-opening to me, and I believe being versatile in styles was a positive thing that helped make BORE the visual delight that it became.</h4> <h4 data-v-68a92ee0>A picture is worth a thousand words so I'll just show you the rest of my work.</h4></div> <div class=\"figures\" data-v-68a92ee0><img"+(_vm._ssrAttr("src",require("assets/img/BORE/version_1_ui.PNG")))+" data-v-68a92ee0> <h4 data-v-68a92ee0>The first version of UI. We didn't use any of this because it felt too different from other aspects of the game.</h4> <img"+(_vm._ssrAttr("src",require("assets/img/BORE/ui_elements.PNG")))+" data-v-68a92ee0> <h4 data-v-68a92ee0>Revised version. Things are coming together.</h4> <img"+(_vm._ssrAttr("src",require("assets/img/BORE/iterations.PNG")))+" data-v-68a92ee0> <h4 data-v-68a92ee0>Other aspects of the art that changed: The cutscenes were redrawn, the characters were redesigned to be more practical and expressive.</h4> <img"+(_vm._ssrAttr("src",require("assets/img/BORE/character changes.PNG")))+" data-v-68a92ee0> <h4 data-v-68a92ee0>The two versions of our in game main characters. They've changed a lot!</h4></div> <h2 data-v-68a92ee0> CONCLUDING THOUGHTS </h2> <div class=\"project-desc col-12 offset-md-2 col-md-8 mt-5\" data-v-68a92ee0><h4 data-v-68a92ee0> BORE was made by an incredibly well working team with lots of love. I am proud of the content our team has created and I'm excited to see where everyone goes from here! To play our game, <a href=\"https://courtneyspivey.itch.io/bore\" data-v-68a92ee0> click here</a>.</h4></div></div>")])}
